import React, { useState, useRef } from "react";
import "./RentalPopup.css";

interface RentalPopupProps {
  open: boolean;
  onClose: () => void;
}

const images = [
  "/rental1.jpg",
  "/rental2.jpg",
  "/rental3.jpg",
  "/rental4.jpg",
  "/rental5.jpg",
  "/rental6.jpg",
  "/rental7.jpg",
  "/rental8.jpg",
  "/rental9.jpg",
  "/rental10.jpg",
  "/rental11.jpg",
];

const RentalPopup: React.FC<RentalPopupProps> = ({ open, onClose }) => {
  const [current, setCurrent] = useState(0);
  const [dragging, setDragging] = useState(false);
  const [popupPos, setPopupPos] = useState<{ x: number; y: number } | null>(null);
  const dragOffset = useRef<{ x: number; y: number }>({ x: 0, y: 0 });
  const popupRef = useRef<HTMLDivElement>(null);

  React.useEffect(() => {
    // 팝업이 열릴 때 화면 크기에 따라 위치 조정
    if (open && popupRef.current) {
      const isMobile = window.innerWidth <= 768;
      setPopupPos({
        x: isMobile ? window.innerWidth / 2 - 250 : window.innerWidth - 500 - 40,
        y: isMobile ? 20 : 40
      });
      setCurrent(0);
    }
  }, [open]);

  const onMouseDown = (e: React.MouseEvent) => {
    if (popupRef.current) {
      setDragging(true);
      const rect = popupRef.current.getBoundingClientRect();
      dragOffset.current = {
        x: e.clientX - rect.left,
        y: e.clientY - rect.top,
      };
    }
  };

  const onMouseMove = (e: MouseEvent) => {
    if (dragging) {
      setPopupPos({
        x: e.clientX - dragOffset.current.x,
        y: e.clientY - dragOffset.current.y,
      });
    }
  };

  const onMouseUp = () => {
    setDragging(false);
  };

  React.useEffect(() => {
    if (dragging) {
      window.addEventListener("mousemove", onMouseMove);
      window.addEventListener("mouseup", onMouseUp);
    } else {
      window.removeEventListener("mousemove", onMouseMove);
      window.removeEventListener("mouseup", onMouseUp);
    }
    return () => {
      window.removeEventListener("mousemove", onMouseMove);
      window.removeEventListener("mouseup", onMouseUp);
    };
    // eslint-disable-next-line
  }, [dragging]);

  // 모바일에서 드래그 방지
  const handleTouchStart = (e: React.TouchEvent) => {
    if (window.innerWidth <= 768) {
      e.preventDefault();
    }
  };

  // 모바일 스타일 적용
  React.useEffect(() => {
    const handleResize = () => {
      const isMobile = window.innerWidth <= 768;
      if (isMobile && popupRef.current) {
        popupRef.current.style.width = '90%';
        popupRef.current.style.left = '50%';
        popupRef.current.style.transform = 'translateX(-50%)';
        popupRef.current.style.top = '20px';
        popupRef.current.style.maxHeight = '80vh';
      } else if (popupRef.current) {
        popupRef.current.style.width = '100%';
        popupRef.current.style.maxWidth = '500px';
        popupRef.current.style.left = `${popupPos?.x ?? window.innerWidth - 500 - 40}px`;
        popupRef.current.style.transform = 'none';
        popupRef.current.style.top = `${popupPos?.y ?? 40}px`;
        popupRef.current.style.maxHeight = '90vh';
      }
    };

    window.addEventListener('resize', handleResize);
    handleResize(); // 초기 로드시에도 실행

    return () => window.removeEventListener('resize', handleResize);
  }, [popupPos]);

  if (!open) return null;

  const prevImage = () => setCurrent((prev) => (prev === 0 ? images.length - 1 : prev - 1));
  const nextImage = () => setCurrent((prev) => (prev === images.length - 1 ? 0 : prev + 1));

  const popupStyle: React.CSSProperties = {
    position: 'fixed',
    top: popupPos?.y ?? 40,
    left: popupPos?.x ?? window.innerWidth - 500 - 40,
    width: '100%',
    maxWidth: '500px',
    height: 'auto',
    maxHeight: '90vh',
    backgroundColor: 'white',
    boxShadow: '0 4px 20px rgba(0, 0, 0, 0.15)',
    borderRadius: '12px',
    zIndex: 1000,
    overflow: 'hidden',
    cursor: 'move'
  };

  return (
    <div className="rental-popup-overlay">
      <div
        className={`rental-popup${dragging ? " dragging" : ""}`}
        ref={popupRef}
        style={popupStyle}
        onMouseDown={onMouseDown}
        onTouchStart={handleTouchStart}
      >
        <div className="rental-popup-header" />
        <button className="close-btn" onClick={onClose}>×</button>
        <div className="carousel-container">
          {images.length > 1 && (
            <button className="carousel-btn left" onClick={prevImage} aria-label="이전 이미지">‹</button>
          )}
          <img
            src={images[current]}
            alt={`임대 이미지 ${current + 1}`}
            className="rental-image"
          />
          {images.length > 1 && (
            <button className="carousel-btn right" onClick={nextImage} aria-label="다음 이미지">›</button>
          )}
        </div>
        <div className="carousel-indicator">
          {current + 1} / {images.length}
        </div>
        <div className="rental-info">
          <h2>임대조건</h2>
          <ul>
            <li>2층 전층 또는 분할도 가능</li>
            <li>2층 150평 (2층 인테리어만 6억)</li>
            <li>엘리베이터 유, 주차장 유</li>
            <li>보증금 1억 (협의가능), 월 임대료 600 (협의가능)</li>
            <li>피부과, 성형외과, 내과 등 모든 과 가능</li>
            <li>공기 임대료 지원 2달</li>
          </ul>
          <div className="rental-location">
            <strong>위치</strong>
            <div style={{ margin: "8px 0" }}>
              경상남도 김해시 구지로 32(내동) 더이로운치과의원 2층
            </div>
            <button
              className="location-btn"
              onClick={() =>
                window.open(
                  "https://map.kakao.com/link/search/경상남도 김해시 구지로 32 더이로운치과의원",
                  "_blank"
                )
              }
            >
              위치 보기
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default RentalPopup; 