import React, { useEffect, useState } from 'react';
import * as XLSX from 'xlsx';

const ITEMS_PER_PAGE = 20;

const OpeningStatus: React.FC = () => {
  const [rows, setRows] = useState<any[]>([]);
  const [headers, setHeaders] = useState<string[]>([]);
  const [currentPage, setCurrentPage] = useState(1);

  useEffect(() => {
    fetch(process.env.PUBLIC_URL + '/hospital.xlsx')
      .then(res => res.arrayBuffer())
      .then(arrayBuffer => {
        const workbook = XLSX.read(arrayBuffer, { type: 'array' });
        const sheetName = workbook.SheetNames[0];
        const worksheet = workbook.Sheets[sheetName];
        const jsonData = XLSX.utils.sheet_to_json(worksheet, { header: 1 });
        if (jsonData.length > 0) {
          setHeaders(jsonData[0] as string[]);
          setRows(jsonData.slice(1));
        }
      })
      .catch(err => {
        alert('엑셀 파일 fetch 에러: ' + err.message);
      });
  }, []);

  // 페이징 계산
  const totalPages = Math.ceil(rows.length / ITEMS_PER_PAGE);
  const startIdx = (currentPage - 1) * ITEMS_PER_PAGE;
  const endIdx = startIdx + ITEMS_PER_PAGE;
  const pageRows = rows.slice(startIdx, endIdx);

  // 페이지네이션 버튼 생성
  const renderPagination = () => (
    <div style={{ marginTop: 32, textAlign: 'center', display: 'flex', justifyContent: 'center', flexWrap: 'wrap', gap: 8 }}>
      <button
        onClick={() => setCurrentPage((p) => Math.max(1, p - 1))}
        disabled={currentPage === 1}
        style={{
          padding: '6px 14px',
          borderRadius: 6,
          border: '1px solid #2563eb',
          background: currentPage === 1 ? '#e5e7eb' : '#fff',
          color: currentPage === 1 ? '#aaa' : '#2563eb',
          fontWeight: 700,
          marginRight: 8,
          cursor: currentPage === 1 ? 'not-allowed' : 'pointer'
        }}
      >◀</button>
      {Array.from({ length: totalPages }, (_, i) => (
        (i + 1 <= 3 || i + 1 > totalPages - 3 || Math.abs(currentPage - (i + 1)) <= 2) ? (
          <button
            key={i + 1}
            onClick={() => setCurrentPage(i + 1)}
            style={{
              margin: '0 2px',
              padding: '6px 14px',
              borderRadius: 6,
              border: i + 1 === currentPage ? '2px solid #2563eb' : '1px solid #ccc',
              background: i + 1 === currentPage ? '#2563eb' : '#fff',
              color: i + 1 === currentPage ? '#fff' : '#222',
              fontWeight: i + 1 === currentPage ? 700 : 400,
              cursor: 'pointer'
            }}
          >
            {i + 1}
          </button>
        ) : (
          (i + 1 === 4 && currentPage > 6) || (i + 1 === totalPages - 2 && currentPage < totalPages - 5)
            ? <span key={i + 1} style={{ padding: '0 6px', color: '#aaa' }}>...</span>
            : null
        )
      ))}
      <button
        onClick={() => setCurrentPage((p) => Math.min(totalPages, p + 1))}
        disabled={currentPage === totalPages}
        style={{
          padding: '6px 14px',
          borderRadius: 6,
          border: '1px solid #2563eb',
          background: currentPage === totalPages ? '#e5e7eb' : '#fff',
          color: currentPage === totalPages ? '#aaa' : '#2563eb',
          fontWeight: 700,
          marginLeft: 8,
          cursor: currentPage === totalPages ? 'not-allowed' : 'pointer'
        }}
      >▶</button>
    </div>
  );

  return (
    <div style={{
      minHeight: '80vh',
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      justifyContent: 'flex-start',
      background: '#f8fafc',
      padding: '40px 0'
    }}>
      <div style={{
        maxWidth: 1300,
        width: '100%',
        background: '#fff',
        borderRadius: 18,
        boxShadow: '0 4px 24px rgba(0,0,0,0.08)',
        padding: '36px 32px 32px 32px',
        margin: '0 auto'
      }}>
        <div style={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          marginBottom: 24
        }}>
          <span style={{
            display: 'inline-block',
            background: '#2563eb',
            color: '#fff',
            fontWeight: 700,
            fontSize: '1.3rem',
            borderRadius: 8,
            padding: '12px 36px',
            boxShadow: '0 2px 8px rgba(37,99,235,0.08)',
            letterSpacing: '-0.5px',
            margin: '0 0 0 0'
          }}>
            병원 개원현황 리스트
          </span>
        </div>
        <div style={{
          textAlign: 'center',
          color: '#888',
          fontSize: 18,
          marginBottom: 24
        }}>
          전국 병원/의원/한의원 개원 현황을 한눈에 확인하세요.
        </div>
        {pageRows.length > 0 ? (
          <div style={{ width: '100%', overflowX: 'auto' }}>
            <table style={{
              borderCollapse: 'collapse',
              width: '100%',
              fontSize: 15,
              background: '#fff',
              borderRadius: 12,
              overflow: 'hidden',
              boxShadow: '0 1px 4px rgba(0,0,0,0.03)'
            }}>
              <thead>
                <tr>
                  {headers.map((key, idx) => (
                    <th key={idx} style={{
                      padding: '12px 8px',
                      fontWeight: 700,
                      fontSize: 16,
                      background: '#f1f5f9',
                      color: '#222',
                      borderBottom: '2px solid #2563eb',
                      minWidth: 120,
                      maxWidth: 220,
                      whiteSpace: 'nowrap',
                      textOverflow: 'ellipsis',
                      overflow: 'hidden',
                      textAlign: 'center'
                    }}>{key}</th>
                  ))}
                </tr>
              </thead>
              <tbody>
                {pageRows.map((row, idx) => (
                  <tr key={startIdx + idx} style={{
                    height: 44,
                    background: idx % 2 === 0 ? '#f9fafb' : '#fff',
                    transition: 'background 0.2s'
                  }}>
                    {headers.map((_, colIdx) => (
                      <td key={colIdx} style={{
                        padding: '10px 8px',
                        fontSize: 15,
                        minWidth: 120,
                        maxWidth: 220,
                        whiteSpace: 'nowrap',
                        overflow: 'hidden',
                        textOverflow: 'ellipsis',
                        textAlign: 'center',
                        borderBottom: '1px solid #e5e7eb'
                      }}>
                        {row[colIdx]}
                      </td>
                    ))}
                  </tr>
                ))}
              </tbody>
            </table>
            {renderPagination()}
          </div>
        ) : (
          <div style={{ textAlign: 'center', color: '#888', fontSize: 18, marginTop: 40 }}>
            로딩 중이거나 데이터가 없습니다.
          </div>
        )}
      </div>
    </div>
  );
};

export default OpeningStatus;