import React, { useState, useEffect, useContext } from 'react';
import { motion } from 'framer-motion';
import { useLocation } from 'react-router-dom';
import { ModalContext } from '../App';
import emailjs from 'emailjs-com';

interface ContactProps {
  isModal?: boolean;
  closeContactModal?: () => void;
}

const Contact: React.FC<ContactProps> = ({ isModal = false, closeContactModal = () => {} }) => {
  const location = useLocation();
  const isStandalonePage = location.pathname === '/contact';

  const [formData, setFormData] = useState({
    name: '',
    email: '',
    phone: '',
    hospitalType: '',
    message: '',
    agreement: false
  });

  const [formStatus, setFormStatus] = useState<{
    success?: boolean;
    message?: string;
  } | null>(null);

  const [showPrivacyModal, setShowPrivacyModal] = useState(false);

  // 새 페이지에서 열렸을 때 스크롤 맨 위로
  useEffect(() => {
    if (isStandalonePage) {
      window.scrollTo(0, 0);
    }
  }, [isStandalonePage]);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleCheckboxChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, checked } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: checked
    }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      await emailjs.send(
        'service_oolt2te', // Service ID
        'template_4ywibrs', // Template ID
        {
          name: formData.name,
          email: formData.email,
          phone: formData.phone,
          hospitalType: formData.hospitalType,
          message: formData.message,
        },
        'public_68vJSjlQ5TvY5no8p' // Public Key
      );
      setFormStatus({
        success: true,
        message: '상담 신청이 완료되었습니다. 빠른 시일 내에 연락드리겠습니다.'
      });
      setFormData({
        name: '',
        email: '',
        phone: '',
        hospitalType: '',
        message: '',
        agreement: false
      });
      if (isModal) {
        setTimeout(() => {
          closeContactModal();
        }, 3000);
      }
    } catch (error) {
      setFormStatus({
        success: false,
        message: '상담 신청 중 오류가 발생했습니다. 잠시 후 다시 시도해주세요.'
      });
    }
  };

  return (
    <div style={{
      background: '#fff',
      borderRadius: '2rem',
      boxShadow: '0 12px 48px rgba(30,41,59,0.13)',
      padding: '2.5rem 1.5rem',
      maxWidth: '700px',
      margin: '0 auto',
      letterSpacing: '0.02em',
      lineHeight: 1.7,
      fontSize: '1.08rem',
      color: '#222',
      fontFamily: 'inherit',
    }}>
      <h2 style={{
        fontSize: '2rem',
        fontWeight: 800,
        color: 'var(--primary-color)',
        marginBottom: '2rem',
        letterSpacing: '0.01em',
        textAlign: 'center',
      }}>문의하기</h2>
      <form onSubmit={handleSubmit} style={{
        display: 'flex',
        flexDirection: 'column',
        gap: '1.5rem',
      }}>
        <div>
          <label htmlFor="name" style={{ display: 'block', marginBottom: '0.5rem', fontWeight: 600 }}>이름</label>
          <input
            type="text"
            id="name"
            name="name"
            value={formData.name}
            onChange={handleChange}
            required
            style={{
              width: '100%',
              padding: '0.75rem',
              borderRadius: '0.5rem',
              border: '1px solid #e2e8f0',
              fontSize: '1rem',
            }}
          />
        </div>

        <div>
          <label htmlFor="email" style={{ display: 'block', marginBottom: '0.5rem', fontWeight: 600 }}>이메일</label>
          <input
            type="email"
            id="email"
            name="email"
            value={formData.email}
            onChange={handleChange}
            required
            style={{
              width: '100%',
              padding: '0.75rem',
              borderRadius: '0.5rem',
              border: '1px solid #e2e8f0',
              fontSize: '1rem',
            }}
          />
        </div>

        <div>
          <label htmlFor="phone" style={{ display: 'block', marginBottom: '0.5rem', fontWeight: 600 }}>전화번호</label>
          <input
            type="tel"
            id="phone"
            name="phone"
            value={formData.phone}
            onChange={handleChange}
            required
            style={{
              width: '100%',
              padding: '0.75rem',
              borderRadius: '0.5rem',
              border: '1px solid #e2e8f0',
              fontSize: '1rem',
            }}
          />
        </div>

        <div>
          <label htmlFor="hospitalType" style={{ display: 'block', marginBottom: '0.5rem', fontWeight: 600 }}>병원 유형</label>
          <select
            id="hospitalType"
            name="hospitalType"
            value={formData.hospitalType}
            onChange={handleChange}
            required
            style={{
              width: '100%',
              padding: '0.75rem',
              borderRadius: '0.5rem',
              border: '1px solid #e2e8f0',
              fontSize: '1rem',
              backgroundColor: 'white',
            }}
          >
            <option value="">선택해주세요</option>
            <option value="general">종합병원</option>
            <option value="specialized">전문병원</option>
            <option value="clinic">의원</option>
            <option value="other">기타</option>
          </select>
        </div>

        <div>
          <label htmlFor="message" style={{ display: 'block', marginBottom: '0.5rem', fontWeight: 600 }}>문의내용</label>
          <textarea
            id="message"
            name="message"
            value={formData.message}
            onChange={handleChange}
            required
            rows={5}
            style={{
              width: '100%',
              padding: '0.75rem',
              borderRadius: '0.5rem',
              border: '1px solid #e2e8f0',
              fontSize: '1rem',
              resize: 'vertical',
            }}
          />
        </div>

        <div style={{ display: 'flex', alignItems: 'center', gap: '0.5rem' }}>
          <input
            type="checkbox"
            id="agreement"
            name="agreement"
            checked={formData.agreement}
            onChange={handleCheckboxChange}
            required
            style={{ width: '1.2rem', height: '1.2rem' }}
          />
          <label htmlFor="agreement" style={{ fontSize: '0.9rem' }}>
            개인정보 수집 및 이용에 동의합니다.
          </label>
        </div>

        {formStatus && (
          <div style={{
            padding: '1rem',
            borderRadius: '0.5rem',
            backgroundColor: formStatus.success ? '#dcfce7' : '#fee2e2',
            color: formStatus.success ? '#166534' : '#991b1b',
          }}>
            {formStatus.message}
          </div>
        )}

        <button
          type="submit"
          disabled={!formData.agreement}
          style={{
            padding: '1rem 2rem',
            backgroundColor: 'var(--primary-color)',
            color: 'white',
            border: 'none',
            borderRadius: '0.5rem',
            fontSize: '1.1rem',
            fontWeight: 600,
            cursor: formData.agreement ? 'pointer' : 'not-allowed',
            opacity: formData.agreement ? 1 : 0.7,
            transition: 'all 0.2s ease',
          }}
        >
          문의하기
        </button>
      </form>
    </div>
  );
};

export default Contact; 