import React, { useState, useEffect, useContext } from 'react';
import { motion } from 'framer-motion';
import { Link } from 'react-router-dom';
import { ModalContext } from '../App';
import logoSvg from '../assets/images/logo.svg';
import './Header.css';

const Header: React.FC = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isScrolled, setIsScrolled] = useState(false);
  const { openContactModal } = useContext(ModalContext);

  useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY > 50) {
        setIsScrolled(true);
      } else {
        setIsScrolled(false);
      }
    };

    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  const handleContactClick = (e: React.MouseEvent) => {
    e.preventDefault();
    openContactModal();
  };

  return (
    <header className={`header ${isScrolled ? 'scrolled' : ''}`}>
      <div className="header-container flex justify-between items-center">
        <motion.div 
          initial={{ opacity: 0, x: -20 }} 
          animate={{ opacity: 1, x: 0 }}
          transition={{ duration: 0.5 }}
        >
          <Link to="/" className="logo-link">
            <img src={logoSvg} alt="탑메디컬 로고" className="logo" />
          </Link>
        </motion.div>

        <nav className="nav">
          <button className="mobile-menu-toggle" onClick={toggleMenu}>
            <i className="fas fa-bars"></i>
          </button>
          
          <motion.ul 
            className={`nav-list ${isMenuOpen ? 'visible' : ''}`}
            initial={{ opacity: 0, y: -10 }} 
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5, delay: 0.2 }}
          >
            <li className="nav-item">
              <Link to="/" className="nav-link">홈</Link>
            </li>
            <li className="nav-item">
              <Link to="/services" className="nav-link">서비스</Link>
            </li>
            <li className="nav-item">
              <Link to="/about" className="nav-link">소개</Link>
            </li>
            <li className="nav-item">
              <Link to="/opening-status" className="nav-link" style={{
                background: '#2563eb',
                color: '#fff',
                borderRadius: 8,
                padding: '8px 20px',
                fontWeight: 700,
                marginLeft: 12,
                boxShadow: '0 2px 8px rgba(37,99,235,0.08)',
                textDecoration: 'none',
                display: 'inline-block'
              }}>개원현황</Link>
            </li>
          </motion.ul>
        </nav>
      </div>
    </header>
  );
};

export default Header; 